.navigation {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  height: 8rem;
  background-color: #fff;
  z-index: 10;
}

.navigation__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 1.6rem;
  height: 100%;
  width: 100%;
}

.navigation__container-logo-name {
  font-weight: 400;
}

.navigation__container-list {
  position: absolute;
  top: 8rem;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding: 2rem 0 2rem 1.6rem;
  width: 100%;
  background-color: #fff;
}

.navigation__container-list-link {
  color: #9d9d9d;
}

.navigation__container-hamburger {
  display: block;
  border: transparent;
  background-color: #fff;
  cursor: pointer;
}

.navigation__container .account {
  display: none;
  border: transparent;
  background: inherit;
}

.active {
  display: block;
}

.hide {
  display: none;
}

.collection__page .navigation__container-logo a {
  color: #fff;
}
.blue-background {
  background-color: #E1EAE4;
}

.darkblue-background {
  background-color: #6d888e;
}

.about__page .navigation__container-list, .about__page .navigation__container-hamburger, .about__page .navigation {
  background-color: #E1EAE4;
}

.collection__page .navigation__container-list, .collection__page .navigation__container-hamburger, .collection__page .navigation {
  background-color: #6d888e;
}

.collection__page .icon  {
  filter: invert(92%) sepia(0%) saturate(7500%) hue-rotate(173deg) brightness(115%) contrast(104%);
}
/* .close-icon {
  display: none;
} */

.account-white {
  display: none;
}

@media screen and (min-width: 1439px) {
  h2 {
    font-size: 4.8rem;
  }
  .text-style {
    font-size: 1.8rem;
  }
  .navigation__container {
    position: relative;
    padding: 0 2.4rem;
  }
  .navigation__container-list {
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 0;
    left: 30rem;
    width: 42rem;
  }
  .navigation__container-list-link {
    padding-right: 2.5rem;
  }
  .navigation__container-hamburger {
    display: none;
  }
  .navigation__container .account {
    display: block;
  }

  .collection__page .navigation__container .account .account-icon {
    display: none;
  }

  .collection__page .navigation__container .account .account-icon-white {
    display: block;
  }

}
